

import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import router from './router'
import vuexI18n from 'vuex-i18n'
import vuxLocales from 'vux/src/locales/all.yml'
import componentsLocales from 'vux/src/locales/components.yml'
import objectAssign from 'object-assign'
import store from './store'
import http from '@/libs/Http.js'
import LocalePlugin from 'vux/src/plugins/locale/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import BusPlugin from 'vux/src/plugins/bus/index.js'
import ConfigPlugin from 'vux/src/plugins/config/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'



Vue.prototype.$http = http
Vue.use(Vuex)
Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)


Vue.prototype.showToast = function (text, width) {
Vue.$vux.toast.show({
text: text,
position: 'bottom',
width: width == null ? '10em' : width,
type: 'text'
})
}


Vue.prototype.showLoading = function (text) {
Vue.$vux.loading.show({
text: text == null ? '加载中...' : text
})
}
Vue.prototype.hideLoading = function () {
Vue.$vux.loading.hide()
}



require('es6-promise').polyfill()

Vue.use(vuexI18n.plugin, store)
Vue.use(LocalePlugin)

const finalLocales = {
'en': objectAssign(vuxLocales['en'], componentsLocales['en']),
'zh-CN': objectAssign(vuxLocales['zh-CN'], componentsLocales['zh-CN'])
}

for (let i in finalLocales) {
Vue.i18n.add(i, finalLocales[i])
}

const nowLocale = Vue.locale.get()
if (/zh/.test(nowLocale)) {
Vue.i18n.set('zh-CN')
} else {
Vue.i18n.set('en')
}


const shouldUseTransition = !/transition=none/.test(location.href)
store.registerModule('vux', {
state: {
direction: shouldUseTransition ? 'forward' : ''
},
mutations: {
updateDirection (state, payload) {
if (!shouldUseTransition) {
return
}
state.direction = payload.direction
}
}
})


Vue.use(ConfigPlugin, {
$layout: 'VIEW_BOX' 
})


Vue.use(AlertPlugin)
Vue.use(BusPlugin)


const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function (...args) {
isPush = true
method.apply(null, args)
}
})


router.beforeEach(function (to, from, next) {
const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)

if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('updateDirection', {direction: 'forward'})
} else {

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', {direction: ''})
} else {
store.commit('updateDirection', { direction: 'reverse' })
}
}
} else {
++historyCount
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.commit('updateDirection', {direction: 'forward'})
}

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1]
window.location.href = `http${url}`
} else {
next()
}
})

router.afterEach(function (to) {
isPush = false
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
store,
el: '#app',
router,
components: { App },
template: '<App/>'
})



